.slot-setting {
  .fixed-tips {
    width: 100%;
    height: 45px;
    background-color: white;
    border-top: 1px solid #666;
    position: fixed;
    left: 0;
    top: 45px;

    p {
      margin: 0;
      padding: 0 0 0 20px;
      height: 100%;
      line-height: 45px;
      font-size: 1.2rem;
      font-weight: bold;

      span {
        padding: 0 10px; }

      span.continue {
        color: #0e80d2; }

      span.warn {
        color: #FF9808; } } }

  .slots {
    margin: 45px 0 0 0;
    padding: 0; }

  .slot-item {
    display: flex;

    .cb-wrapper {
      width: 30px; }

    .slot-no {
      width: 100px; }

    .msg {
      color: #FF6425; }

    .sensor-dot {
      display: inline-block;
      background-color: #108EE9;
      margin: 0 5px;
      border-radius: 50% 50%;
      height: 25px;
      width: 25px;
      text-align: center;
      padding: 3px;
      color: white; } } }
